export const NiDataConnection = class {
    constructor() {
        pri(this);
        this.isOpen = false;
        this.transaction = false;
        this.params = {};
    }
    setParams(paras) {
        V.merge(this.params, paras, true);
    }
    async open() {
        const {
            _,
            __
        } = pri(this);
        _.isOpen = true;
    }
    async close() {
        const {
            _,
            __
        } = pri(this);
        _.isOpen = false;
    }
    async invoke(cmd) {
        return [];
    }
    async commit() {
        return [];
    }
};
