export const NiDataCommand = class {
    constructor() {
        const {
            _,
            __
        } = pri(this);
        _.connection = null;
        _.command = '';
        _.params = {
            dbtype: 'json'
        };
    }
    async execute() {
        const {
            _,
            __
        } = pri(this);
        if (!_.connection || !_.connection.isOpen) {
            V.showException('');
            throw new Error('');
        } else {
            try {
                //
                return await _.connection.invoke(_);
            } catch (e) {
                V.showException('connection invoke success', e);
                throw e;
            }
        }
    }
    async excute() {
        return await this.execute.apply(this, arguments);
    }
};

