/**
 * NiUrlDataFactory
 * URL
 */
export const NiUrlDataFactory = class extends NiDataFactory {
    constructor() {
        super();
        const that = this;
        that.regex = new RegExp('^((http|https):\\/\\/[\\w\\-_]+(\.[\\w\\-_]+)+([\\w\\-\\.,@?^=%&amp;:/~\\+#]*[\\w\\-\\@?^=%&amp;/~\+#])?)$', 'i');
        const {
            _,
            __
        } = pri(that, {
            UrlConnection: class extends NiDataConnection {
                constructor() {
                    super();
                    const thats = this;
                    const {
                        _,
                        __
                    } = pri(this, {
                        cmds: [],
                        host: "",
                        port: "",
                        method: "",
                        timeout: 60000,
                        invoke: async(command, params) => {
                            const {
                                _,
                                __
                            } = pri(thats);
                            try {
                                let args = (params.args ? params.args.value : params);
                                for (let i in args) {
                                    if (args[i].value) args[i] = args[i].value;
                                }
                                const func = (function() {
                                    switch ((args['_method'] || __.method).toLowerCase()) {
                                        case 'get':
                                            delete args['_method'];
                                            args = Q.stringify(args);
                                            return N.get;
                                        case 'post':
                                        default:
                                            delete args['_method'];
                                            args = Q.stringify(args);
                                            return N.post;
                                        case 'postjson':
                                            delete args['_method'];
                                            return N.postJson;
                                    }
                                })(); //
                                let data = [];
                                await V.each(command.split(';'), async v => {
                                    v = v.trim();
                                    if (v.indexOf(':') < 0 && v.indexOf('//') < 0)
                                        v = (function() {
                                            let h = __.host.length > 0 ? __.host[V.random() % __.host.length] : __.host[0];
                                            if (h.indexOf(':') < 0 && h.indexOf('//') < 0)
                                                h = 'http://' + h;
                                            return h;
                                        })() + (V.isValid(__.port) && (__.port + "") != '80' ? ":" + __.port : "") + v;
                                    if (that.regex.test(v)) {
                                        const text = (await func(v, args)) || '';
                                        try {
                                            data.push([V.json(text)]);
                                        } catch (e) {
                                            data.push([{ text: text }]);
                                        }
                                    } else
                                        throw new Error(`${v}`);
                                    return false;
                                }, true);
                                return data;
                            } catch (e) {
                                console.log(command, params);
                                console.log(e.message);
                                throw e;
                            }
                        }
                    });
                }
                async open() {
                    const {
                        _,
                        __
                    } = pri(this);
                    __.host = (_.params.host || '').split(';');
                    __.port = _.params.port || 80;
                    __.method = _.params.method || 'POST';
                    __.timeout = _.params.timeout || 60000;
                    await super.open();
                }
                async close() {
                    await super.close();
                }
                async invoke(cmd) {
                    const {
                        _,
                        __
                    } = pri(this);
                    if (_.transaction) {
                        __.cmds.push({
                            command: cmd.command,
                            params: cmd.params
                        });
                    } else
                        return await __.invoke(cmd.command, cmd.params);
                }
                async commit() {
                    const {
                        _,
                        __
                    } = pri(this);
                    if (_.transaction) {
                        //
                        let data = [];
                        let cmds = __.cmds;
                        __.cmds = [];
                        await V.each(cmds, async v => {
                            data.push(await __.invoke(v.command, v.params));
                            return false;
                        }, true);
                        return data;
                    } else
                        throw new Error('');
                }
            }
        });
    }
    createDBConnection() {
        const {
            _,
            __
        } = pri(this);
        return new __.UrlConnection();
    }
};

